<script setup lang="ts">
import { ref, watch } from 'vue'
import { ElForm, ElMessage } from 'element-plus'
import { getOrderDetail, updateOrderStatus } from '../../api/orderManagement.ts'

const visible = ref(false)
const formRef = ref<InstanceType<typeof ElForm>>()
// const loading = ref(false)
// const logsLoading = ref(false)

// 订单流转日志数据
// const orderLogs = ref([])

// 表单数据
const form = ref({
  id: 0,
  order_no: '',
  merchant_order_no: '',
  third_party_order_no: '',
  trace_id: '',
  merchant_id: '',
  product_id: '',
  channel_id: '',
  amount: 0,
  fee: 0,
  status: 1,
  payment_method: '',
  notify_url: '',
  return_url: '',
  client_ip: '',
  user_agent: '',
  subject: '',
  body: '',
  extra_data: {},
  third_party_response: {},
  notify_count: 0,
  notify_status: 0,
  expire_time: '',
  paid_time: '',
  created_at: '',
  updated_at: ''
})

// 订单状态选项
const statusOptions = [
  { label: '待支付', value: 1 },
  { label: '支付中', value: 2 },
  { label: '支付成功', value: 3 },
  { label: '支付失败', value: 4 },
  { label: '已退款', value: 5 },
  { label: '已关闭', value: 6 }
]

// 通知状态选项
const notifyStatusOptions = [
  { label: '未通知', value: 0 },
  { label: '通知成功', value: 1 },
  { label: '通知失败', value: 2 }
]

// 打开弹窗
const open = async (data: any = {}) => {
  if (data.id) {
    // 获取订单详情
    try {
      const res = await getOrderDetail(`/api/v1/admin/order/${data.id}`)
      const { status, data: orderData } = res.data
      if (status && orderData) {
        Object.assign(form.value, orderData)
        // 转换金额显示（分转元）
        form.value.amount = orderData.amount / 100
        form.value.fee = orderData.fee / 100
      }
    } catch (error: any) {
      ElMessage.error('获取订单详情失败: ' + (error?.message || error))
      return
    }
  } else {
    Object.assign(form.value, {
      id: data.id || 0,
      order_no: data.order_no || '',
      merchant_order_no: data.merchant_order_no || '',
      third_party_order_no: data.third_party_order_no || '',
      trace_id: data.trace_id || '',
      merchant_id: data.merchant_id || '',
      product_id: data.product_id || '',
      channel_id: data.channel_id || '',
      amount: data.amount ? data.amount / 100 : 0,
      fee: data.fee ? data.fee / 100 : 0,
      status: data.status ?? 1,
      payment_method: data.payment_method || '',
      notify_url: data.notify_url || '',
      return_url: data.return_url || '',
      client_ip: data.client_ip || '',
      user_agent: data.user_agent || '',
      subject: data.subject || '',
      body: data.body || '',
      extra_data: data.extra_data || {},
      third_party_response: data.third_party_response || {},
      notify_count: data.notify_count || 0,
      notify_status: data.notify_status ?? 0,
      expire_time: data.expire_time || '',
      paid_time: data.paid_time || '',
      created_at: data.created_at || '',
      updated_at: data.updated_at || ''
    })
  }
  visible.value = true
}

// 提交表单
const handleSubmit = async () => {
  await formRef.value?.validate()
  
  // 转换金额（元转分）
  const payload = { 
    ...form.value,
    amount: Math.round(form.value.amount * 100),
    fee: Math.round(form.value.fee * 100)
  }
  
  const res = await updateOrderStatus('/api/v1/admin/order/update', payload)
  const { status, message } = res.data
  if (status) {
    ElMessage.success(message || '更新成功')
    emit('refresh')
    visible.value = false
  } else {
    ElMessage.error(message || '更新失败')
  }
}

// 重置表单
const resetForm = () => {
  form.value = {
    id: 0,
    order_no: '',
    merchant_order_no: '',
    third_party_order_no: '',
    trace_id: '',
    merchant_id: '',
    product_id: '',
    channel_id: '',
    amount: 0,
    fee: 0,
    status: 1,
    payment_method: '',
    notify_url: '',
    return_url: '',
    client_ip: '',
    user_agent: '',
    subject: '',
    body: '',
    extra_data: {},
    third_party_response: {},
    notify_count: 0,
    notify_status: 0,
    expire_time: '',
    paid_time: '',
    created_at: '',
    updated_at: ''
  }
}

// 关闭弹窗时重置表单
watch(visible, (newVal) => {
  if (!newVal) resetForm()
})

const emit = defineEmits<{
  (event: 'refresh'): void
}>()

defineExpose({ open })
</script>

<template>
  <el-drawer v-model="visible" title="订单详情" size="70%">
    <el-form :model="form" ref="formRef" label-width="150px">
      <el-divider content-position="left">基本信息</el-divider>

      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item label="订单号">
            <el-input v-model="form.order_no" readonly />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="商户订单号">
            <el-input v-model="form.merchant_order_no" readonly />
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item label="第三方订单号">
            <el-input v-model="form.third_party_order_no" readonly />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="追踪ID">
            <el-input v-model="form.trace_id" readonly />
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item label="商户ID">
            <el-input v-model="form.merchant_id" readonly />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="产品ID">
            <el-input v-model="form.product_id" readonly />
          </el-form-item>
        </el-col>
      </el-row>

      <el-divider content-position="left">金额信息</el-divider>

      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item label="订单金额(元)">
            <el-input v-model="form.amount" readonly />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="手续费(元)">
            <el-input v-model="form.fee" readonly />
          </el-form-item>
        </el-col>
      </el-row>

      <el-divider content-position="left">状态信息</el-divider>

      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item label="订单状态">
            <el-select v-model="form.status" :disabled="form.status === 3">
              <el-option
                v-for="option in statusOptions"
                :key="option.value"
                :label="option.label"
                :value="option.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="通知状态">
            <el-select v-model="form.notify_status">
              <el-option
                v-for="option in notifyStatusOptions"
                :key="option.value"
                :label="option.label"
                :value="option.value"
              />
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item label="支付方式">
            <el-input v-model="form.payment_method" readonly />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="通知次数">
            <el-input v-model="form.notify_count" readonly />
          </el-form-item>
        </el-col>
      </el-row>

      <el-divider content-position="left">订单内容</el-divider>

      <el-form-item label="订单标题">
        <el-input v-model="form.subject" readonly />
      </el-form-item>

      <el-form-item label="订单描述">
        <el-input v-model="form.body" type="textarea" :rows="3" readonly />
      </el-form-item>

      <el-divider content-position="left">回调信息</el-divider>

      <el-form-item label="通知URL">
        <el-input v-model="form.notify_url" readonly />
      </el-form-item>

      <el-form-item label="返回URL">
        <el-input v-model="form.return_url" readonly />
      </el-form-item>

      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item label="客户端IP">
            <el-input v-model="form.client_ip" readonly />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="用户代理">
            <el-input v-model="form.user_agent" readonly />
          </el-form-item>
        </el-col>
      </el-row>

      <el-divider content-position="left">时间信息</el-divider>

      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item label="过期时间">
            <el-input v-model="form.expire_time" readonly />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="支付时间">
            <el-input v-model="form.paid_time" readonly />
          </el-form-item>
        </el-col>
      </el-row>

      <el-row :gutter="20">
        <el-col :span="12">
          <el-form-item label="创建时间">
            <el-input v-model="form.created_at" readonly />
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item label="更新时间">
            <el-input v-model="form.updated_at" readonly />
          </el-form-item>
        </el-col>
      </el-row>

      <el-divider content-position="left">扩展数据</el-divider>

      <el-form-item label="扩展数据">
        <el-input 
          :value="JSON.stringify(form.extra_data, null, 2)" 
          type="textarea" 
          :rows="4" 
          readonly 
        />
      </el-form-item>

      <el-form-item label="第三方响应">
        <el-input 
          :value="JSON.stringify(form.third_party_response, null, 2)" 
          type="textarea" 
          :rows="4" 
          readonly 
        />
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="handleSubmit">保存修改</el-button>
        <el-button @click="visible = false">取消</el-button>
      </el-form-item>
    </el-form>
  </el-drawer>
</template>
